import React from 'react';
import searchStateSVG from '../public/curiosity search-rafiki.svg';


const SearchMovieState = () => {
  return (
    <div style={{width: 400, display: 'flex', flexDirection: 'column', justifyContent: 'center'}}>
      <img src={searchStateSVG} alt="Imagem filme no encontrado" />
      <p style={{textAlign: 'center'}}>No conseguimos encontrar nenhum filme com esse ttulo. Tente palavras diferentes ou verifique se o ttulo est correto.</p>
    </div>
  );
};

export default SearchMovieState;
